import * as yup from 'yup';
import { phoneRegExp } from './regex';

export const TableFormSchema = yup.object().shape({
	tableNum: yup
		.number()
		.typeError(',    ')
		.positive(',    ')
		.required(',   ')
		.min(1, ',    ')
		.max(99, ',    '),
});

export const ReviewFormSchema = yup.object().shape({
	name: yup.string(),
	phone: yup
		.string()
		.matches(phoneRegExp, {
			message: ',    ',
			excludeEmptyString: true,
		}),
	content: yup.string().required(',  '),
});
